.benefits {
  background: $color-light-grey;
  padding: 87px 0;
}

.benefits__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  max-width: 1184px;
  width: 100%;
}

.benefits__item {
  background: $color-white;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px flex, 1 1 auto;
  height: 370px;
  margin: 18px 30px 18px;
  overflow: hidden;
}

.benefits__item--wide {
  display: flex;
  width: 1124px;
}

.benefits__item--copy {
  width: 623px;
}

.benefits__item--media {
  height: auto;
  position: relative;
  width: 440px;
}

.benefits__item--media::before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: bottom;
}

.benefits__item--media img {
  height: 56%;
  margin-bottom: 62px;
  margin-left: 62px;
  max-width: 100%;
  object-fit: contain;
  vertical-align: bottom;
  width: 67%;
}

.benefits__item__text {
  display: flex;
  flex: 0 1 476px;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  max-width: 50%;
  padding: 0 36px;
}

.benefits__item--copy .benefits__item__text {
  max-width: unset;
}

.benefits__item__text h2 {
  line-height: 1.16em;
  margin: 0 0 30px 0;
  text-align: left;
}

.benefits__item__text p {
  font-family: $font-open-sans;
  font-size: 1rem;
  line-height: 1.375em;
  padding-bottom: 50px;
  position: relative;
}

.benefits__item__text p::after {
  bottom: 0;
  content: "";
  display: block;
  height: 4px;
  left: 0;
  max-width: 327px;
  position: absolute;
  width: 80%;
}

.benefits__item__image {
  flex: 1 1 auto;
  position: relative;
}

.benefits__item__image img {
  height: 100%;
  left: 50%;
  max-width: 100%;
  object-fit: contain;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  width: auto;
}

.benefits__item__image--square::before {
  border-radius: 41px;
  -webkit-border-radius: 41px;
  -moz-border-radius: 41px;
  -ms-border-radius: 41px;
  -o-border-radius: 41px;
  content: "";
  display: block;
  height: 200px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(0%, -60%) rotate(45deg);
  -webkit-transform: translate(0%, -60%) rotate(45deg);
  -moz-transform: translate(0%, -60%) rotate(45deg);
  -ms-transform: translate(0%, -60%) rotate(45deg);
  -o-transform: translate(0%, -60%) rotate(45deg);
  transform-origin: 50% 50%;
  width: 200px;
}

.benefits__item__image--square img {
  height: 219px;
  transform: translate(-50%, -30%);
  -webkit-transform: translate(-50%, -30%);
  -moz-transform: translate(-50%, -30%);
  -ms-transform: translate(-50%, -30%);
  -o-transform: translate(-50%, -30%);
}

.benefits__item__image--circle::before {
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
  -o-border-radius: 100%;
  content: "";
  display: block;
  height: 320px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-75%, -55%);
  -webkit-transform: translate(-75%, -55%);
  -moz-transform: translate(-75%, -55%);
  -ms-transform: translate(-75%, -55%);
  -o-transform: translate(-75%, -55%);
  transform-origin: 50% 50%;
  width: 320px;
}

.benefits__item__image--circle img {
  height: 328px;
}

.btn--redish,
.benefits__item--red.benefits__item--media,
.benefits__item--red .benefits__item__image::before,
.benefits__item--red .benefits__item__text p::after {
  background-color: $color-redish;
}

.btn--primary,
.benefits__item--blue.benefits__item--media,
.benefits__item--blue .benefits__item__image::before,
.benefits__item--blue .benefits__item__text p::after {
  background-color: $color-primary;
}

.btn--green,
.benefits__item--green.benefits__item--media,
.benefits__item--green .benefits__item__image::before,
.benefits__item--green .benefits__item__text p::after {
  background-color: $color-green;
}